.accordion {
    max-width: 80vw;
}

.accordion-item {
    list-style: none;
    color: #333;
}

.accordion-toggle {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgb(240, 240, 240);
    padding: 1em;
    color: #333;
    border: #1B611A;
    border-style: solid;
    border: 1px;
    margin: 10px;
    border-radius: 25px;
}

.accordion-toggle:hover{
    background-color: #7FA66F;
    transition: 5;
    color: white;
}

.accordion-toggle h3 {
    margin: 0;
}

.accordion-content {
    background-color: rgb(255, 255, 255);
    padding: 1em;
}