a:-webkit-any-link {
  text-decoration: none;
  color: #1B611A;
  cursor: pointer;
}
div, p{
 font-size: 2vh;
}
html {
  scroll-behavior: smooth;
}

/*
  Configuracion del Logo
*/

.logo{
  display: block;
  margin: auto;
  display: inline-block;
}

/*
  Configuracion de la barra de navegacion 
*/

.navList{

  position: fixed;


  background-color: rgba(239, 239, 239, 0.8);
  display: grid;
  grid-template-columns: 30% 20% 50%;
  font-size: x-large;
  font-weight: bold;
  list-style: none;
  text-align: center;

  margin: 0%;
  padding: 10px;
  width: 100%;

  border-style: none;
}

.navList .itemGrid{
  grid-column: 3;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.navList .itemGrid .item{
  height: 100%;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  justify-self: center;
}

.navList .itemGrid .item:hover{
  border-width: 0 0 0.4vh 0;
  border-style: solid;
  color: #7FA66F;
}

/*
  Configuracion del carrusel
*/


.sliderItem{
  display: block;
  margin: auto;
  width: 100%;
  height: 82vh;
}

/*
  Configuracion del apartado Sobre Nosotros
*/

.aboutBox{
  background-color: #7FA66F;
  color: white;
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  margin: 0px;
}

.aboutBox .subTitle{
  text-align: center;
}

.aboutBox .aboutText{
  text-align: center;
}

.moreBtn{
  background-color: white;
  color: #333;
  display: block;
  margin: auto;
  padding: 2vh 2vw;

  border-width: thick;
  border-style: groove;
  border-color: rgba(0, 0, 0, 0.307);
  border-radius: 15%;

  font-weight: bolder;

}

.doubleArrows{
  display: block;
  margin: 0px 0px 8vh 0px;
  padding: 0px;
  width: 100%;
  height: 5vh;
}
.wavesNegative{
  display: block;
  margin: 8vh 0px 0px 0px;
  padding: 0px;
  width: 100%;
  height: 5vh;
}


/*
  Configuracion del apartado de Servicios
*/

.serviceBox{
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  margin: 0px 6.5vw;
}

.serviceTitle{
  color: #333;
  text-align: center;
}

.serviceItem{
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  justify-content: center;
}

.serviceItem .subTitle{
  color: #333;
  margin: 5px;
}

.serviceItem .logo{
  width: 22vw;
  height: 12vw;
  border-radius: 5%;
}

.serviceItem .serviceText{
  margin: 1vh 2vw;
  width: 22vw;
  color: #333;
  text-align: center;
}

.btn{
  background-color: white;
  color: #333;
  border-color: rgba(0, 0, 0, 0.307);
  border-style: solid;
  border-radius: 15%;
  border-end-start-radius: 5px;
  border-start-start-radius: 5px;
  border-end-end-radius: 5px;
  border-start-end-radius: 5px;
  font-weight: bolder;
  display: block;
  margin: 15px auto;
  width: 8vw;
  height: 3vh;
  text-align: center;
  padding: 10px;
}

.btn:hover{
  background-color: rgb(209, 248, 209);
  color: rgb(31, 31, 31); 
  border-color: rgba(43, 43, 43, 0.336);
}

/*
  Configuracion del apartado de Ubicacion
*/

.separator{
  display: block;
  margin: 0px;
  width: 100%;
  bottom: 5px;
  height: 5vh;
  padding: 0px;
  margin: 8vh 0px -1px 0px;
  transform: rotate(180deg);
  padding: 0 0 4vh 0;
}

.locationBox{
  background-color: #7FA66F;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 0px;
  padding: 0px;
  align-self: center;
}


.locationLeftColumn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.locationLeftColumn .content{
  width: 100%;
}

.locationBox .subtitle{
  display: flex;
  align-self: center;
  justify-content: center;
  color: white;
 

}
.locationBox .map{
  padding: 10px;
  display: flex;
  justify-content: center;


}
iframe{
  border-radius: 25px;
}

.locationRightColumn{
  color: white;
  padding: 5vh 10vw;
  text-align: center;
  justify-content: center;
}
.locationRightColumn p{
  text-align: justify;
}

.locationRightColumn img{
  border-radius: 8%;
  margin: auto;
}




.bodySeparator{
  height: 4vh;
  background-color: #7FA66F;
}

.footer{
  display: flex;
  background-color: #333333;
  height: 6vh;
}

.footer p{
  font-size: 1.8vh;
  margin: auto;
  text-align: center;
  vertical-align: middle;
  color: whitesmoke;
}