.imgslider{
    width: auto;
    height: 40vw;
    
}


#container{
    width: 100%;
    margin: 0px;
    overflow: hidden;
    padding: 0px;
    margin: 0px;
    background-color: #7FA66F;
}

#container ul{
    list-style: none;
    width: 100%;
    display: flex;
    animation: slide 40s infinite;
    padding: 0;
    margin: 0;
   
}

@keyframes slide{
    0% {margin-left: 0px;}
    10% {margin-left: 0px;}

    12.5% {margin-left: -103vw;}
    22.5% {margin-left: -103vw;}

    25% {margin-left: -206vw;}
    35% {margin-left: -206vw;}

    37.5% {margin-left: -309vw;}
    47.5% {margin-left: -309vw;}

    50% {margin-left: -412vw;}
    60% {margin-left: -412vw;}

    62.5% {margin-left: -309vw;}
    72.5% {margin-left: -309vw;}

    75% {margin-left: -206vw;}
    85% {margin-left: -206vw;}

    87.5% {margin-left: -103vw;}
    97.5% {margin-left: -103vw;}

    100% {margin-left: 0px;}
}