.separator{
    background-color: #7FA66F;
    height: 5vh;
}


.faqGrid{
    display: grid;
    grid-template-columns: 60% 40%;
}

.faqBox{
    margin-left: 5vw;
    margin-right: 5vw;
}

.faqBox .item .answer{
    padding: 2vh 2vw;
    border-style: solid;
    border-width: thin;

    border-top: none;
    border-right: none;
    border-bottom: none;
}

.askBox .title{
    text-align: center;
    color: #333;
}

.askBox .text{
    margin: 1vh 5vw;
    color: #333;
}

.btn{
  background-color: white;
  color: #333;
  border-color: rgba(0, 0, 0, 0.307);
  border-style: solid;
  border-radius: 15%;
  border-end-start-radius: 5px;
  border-start-start-radius: 5px;
  border-end-end-radius: 5px;
  border-start-end-radius: 5px;
  font-weight: bolder;
  display: block;
  margin: 15px auto;
  width: 8vw;
  height: 3vh;
  text-align: center;
  padding: 10px;
}

.btn:hover{
    background-color: rgb(209, 248, 209);
    color: rgb(31, 31, 31); 
    border-color: rgba(43, 43, 43, 0.336);
  }

.askBox .btn:active{
    border-width: thick;
    border-style: groove;
    border-color: #1B611A;
    background-color: #ecffe4;
}
