.separator{
    background-color: #7FA66F;
    height: 5vh;
}

.grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);


    border-right: none;
    border-left: none;
    border-width: thin;
}

.grid .phones{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.grid .subtitle{
    background-color: #7FA66F;
    color: white;
    margin: 0.1vh 5vw;
    padding: 0.1vh 0.5vw;
    grid-column: span 2;
}

.grid .text{
    /* background-color: #96ae60; */
    color: #333;
    padding: 0.5vh 2vw;
    margin-right: 10vw;
    font-size: 18px;
/* 
    border-style: solid;
    border-color: #6b9217; */
}

.grid .item{
    margin: 0.1vh 5vw;
    grid-column: span 2;
    display: grid;
    grid-template-columns: 10% 90%;
}

.grid .email{
    grid-column: span 2;
}

.grid .message{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.grid .message .title{
    background-color: #7FA66F;
    color: white;
    width: 87%;
    padding: 0.1vh 0.5vw;
    margin: 0px;
    grid-column: span 2;
}

.grid .message .input{
    display: block;
    margin: center;
    padding: 2vh 2vw;
}

.grid .message .shortField{
    margin: 0.5vh 0px;
}

.grid .message .shortField .input{
    width: 77%;
    border-style: solid;
    border-width: thin;
    border-color: #1B611A;
    font-size: 18px;
}

.grid .input:focus , .grid .textarea:focus {
    background-color: #ecffe4;
}


.grid .message .longField{
    margin: 0.5vh 0px;
    grid-column: span 2;
}

.grid .message .longField .input{
    width: 88.5%;
    border-style: solid;
    border-width: thin;
    border-color: #1B611A;
    font-size: 18px;
    
}

.grid .textareaBox{
    grid-column: span 2;
    display: block;
    margin: 0px;
    padding: 0px;
    
}

.grid .textarea{
    margin: 1vh 0px;
    width: 88.5%;
    height: 20vh;
    border-color: #1B611A;
    font-size: 18px;
    padding: 2vh 2vw;
}

.grid .submit{
    background-color: white;
    color:  #333;
    font-weight: bolder;
    grid-column: span 2;
    display: block;
    width: 25vw;
    margin: 1vh 10vw;
    padding: 1vh 1vw;
    font-size: 18px;

    border-width: thick;
    border-style: groove;
    border-color: rgba(0, 0, 0, 0.307);
    border-radius: 15%;
    border-end-start-radius: 5px;
    border-start-start-radius: 5px;
    border-end-end-radius: 5px;
    border-start-end-radius: 5px;

}


.grid .submit:hover{
    cursor: pointer;
    background-color: rgb(209, 248, 209);
    color: rgb(31, 31, 31); 
    border-color: rgba(43, 43, 43, 0.336);
}

.icon{
    display: block;
    margin: auto;
    width: 2.5vw;
    height: 5vh;
}

.mapBox{
    background-color: #7FA66F;
    color: white;
    padding: 2vh 0 4vh 0;
    text-align: center;
}

.mapBox .title{
    margin: 0 0 4vh 0;
    text-align: center;
}

.mapImage{
    display: block;
    margin: auto;
   

}