.upperGrid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1px;
    background-color: #7FA66F;
    color: white;
}

 .mainText{
    margin: 0px 20vw;
    grid-column: span 2;
    padding: 3vw 3vh;
}

.upperGrid .container{
    height: 30vh;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content:center;
    padding: 0 5vw;


}

.upperGrid .subtitle{
    margin: 0px 5vw;
    text-align: left;
    
    
}

.upperGrid .text{
    margin: 1vh 5vw;
    text-align: justify;    
    
}

.upperGrid .imageContainer{
    height: 30vh;
    text-align: center;

}

.upperGrid .gridImage{
    height: 30vh;
    border-radius: 5%;
}


.middleGrid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.middleGrid .title{
    margin: 2vh 0px 2vh 0px;
    grid-column: span 3;
    text-align: center;

    border-width: thin;
    border-right: none;
    border-left: none;
    color: #333;
}

.middleGrid .subtitle{
    display: flex;
    align-content: center;
    align-self: center;
    align-items: center;
    justify-content: center;
    color: #333;

    margin: 0.1vw 0.1vh;
    height: 15vh;
    text-align: center;
}

.middleGrid .text{
    text-align: center;
    margin: auto;
    width: 20vw;
    color: #333;


}


.middleGrid .gridImage, .bottomGrid .gridImage{
    display: block;
    margin: auto;
    width: 20vw;
    height: 20vh;
    border-radius: 5%;
}

.bottomGrid{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.bottomGrid .title{
    
    margin: 2vh 0px 2vh 0px;
    grid-column: span 4;
    text-align: center;
    color: #333;


    border-width: thin;
    border-right: none;
    border-left: none;
}

.bottomGrid .subtitle{

    display: flex;
    align-content: center;
    align-self: center;
    align-items: center;
    justify-content: center;
    color: #333;

    margin: 0.1vw 0.1vh;
    height: 15vh;
    text-align: center;
}

.bottomGrid .text{
    text-align: center;
    margin: auto;
    width: 20vw;
    color: #333;

}
.item {
    margin: 0px 0px 4vh 0px;

}
