
.grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    
  
}

.mainText{
    text-align: center;
    grid-column: span 2;
    padding: 0vw 4vh;
    color: #333;
    background-color: white;
    margin-top: 4vh;
    font-style: oblique;

   
}

.container{
    height: 30vh;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content:center;
    background-color: #7FA66F;
    padding: 0 5vw;


}

.container .subtitle{
    margin: 0px 5vw;
    text-align: left;
    color: white;
}

.container .text{
    margin: 1vh 5vw;
    color: white;
    text-align: justify;

}
.bottomContainer .subtitle{
    margin: 0px 5vw;
    text-align: left;
    color: #333;
}
.bottomContainer .text{
    margin: 1vh 5vw;
    color: #333;
}


.imageContainer{
    height: 30vh;
    background-color: #7FA66F;
    text-align: center;

}

.gridImage{
    height: 30vh;
    border-radius: 5%;
}

.gridSeparator{
    grid-column: span 2;
    text-align: center;
    color: #333;
    border-left: none;
    border-right: none;

}

.bottomContainer{ 
    height: 30vh;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content:center;
}

.bottomContainer .subtitle{
    margin: 1vh 5vw;
    text-align: left;
}